<template>
  <div>
    <el-table
      border
      :data="dataList"
      :highlight-current-row="true"
      style="width: 100%"
      :max-height="600"
      :default-sort = "{prop: 'important', order: 'descending'}">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-table
            :data="props.row.tasks"
            border
            style="width: 100%">
            <el-table-column
              prop="type"
              label="类型"
              width="100">
            </el-table-column>
            <el-table-column
              prop="estimate"
              label="故事点"
              width="90">
            </el-table-column>
            <el-table-column
              prop="userId"
              :formatter="userFormatter"
              label="用户">
            </el-table-column>
            <el-table-column
              prop="startDate"
              :formatter="dateFormatter"
              label="开始日期">
            </el-table-column>
            <el-table-column
              prop="endDate"
              :formatter="dateFormatter"
              label="完成日期">
            </el-table-column>
          </el-table>
        </template>
      </el-table-column>
      <el-table-column
        prop="id"
        label="ID"
        width="70">
      </el-table-column>
      <el-table-column
        prop="name"
        label="名称">
      </el-table-column>
      <el-table-column
        prop="important"
        label="重要级别"
        sortable
        width="110">
      </el-table-column>
      <el-table-column
        prop="track"
        label="类别"
        width="150">
      </el-table-column>
      <el-table-column
        prop="estimate"
        label="故事点"
        width="70">
      </el-table-column>
      <el-table-column
        prop="uiUserId"
        label="设计"
        :formatter="userFormatter"
        width="80">
      </el-table-column>
      <el-table-column
        prop="note"
        width="120"
        label="备注">
      </el-table-column>
      <el-table-column label="操作" width="60">
        <template slot-scope="scope">
          <div class="backlog-operator">
            <i @click="onBacklogView(scope.row)" class="fa fa-eye"></i>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <view-html ref="viewHtml"></view-html>
  </div>
</template>

<script>
import ViewHtml from '../../../components/quill-editor/quillEditorView'
export default {
  components: {
    ViewHtml
  },
  props: [
    'sprintId'
  ],
  data () {
    return {
      tableName: 'backlogs'
    }
  },
  computed: {
    userList () {
      return this.$store.state.userList
    }
  },
  methods: {
    initData () {
      this.$nextTick(() => {
        this.findCondition = {
          sprintId: this.sprintId
        }
        this.findDataByCondition()
      })
    },
    onBacklogView (row) {
      this.$refs.viewHtml.show(row.how)
    },
    onAddData () {
      this.$router.push({ name: 'detail' })
    },
    onDelete (data) {
      // 移除
    }
  }
}
</script>

<style scoped lang="scss">
  .backlog-operator{
    font-size: 18px;

    .fa{
      margin-left: 5px;
      cursor: pointer;
    }

    .fa:hover{
      color: red;
      transform: scale(1.1)
    }
  }
</style>



